@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100;200;400;500;600;700&display=swap);
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

@media print {
    body {
        height: 29.7cm;
        width: 21cm;
        padding: 10mm 10mm 0mm 10mm;
    }
}

html{font-size:18px}.App{background-image:linear-gradient(to top, #30cfd0 0%, #330867 100%);background-size:400% 400%;animation:gradient 15s ease infinite;display:flex;flex-direction:column;align-items:center;justify-content:center}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@media print{html{font-size:15px}.App{margin:0;padding:0}}@media screen and (max-width: 768px){html{font-size:16px}}
.bio{width:100%}.full-name{font-size:2.5rem;line-height:3rem;text-transform:uppercase;text-align:left;border-bottom:4px solid #c9d1d9}.first-name{font-weight:800}.first-name::after{content:" "}.last-name{font-weight:200}.contact-info{font-size:.8rem;font-weight:300;display:flex;flex-direction:row;justify-content:space-between;align-items:flex-end;margin:.15rem 0 1rem}.about{font-size:1rem;text-align:justify}
.school{margin-bottom:1rem}.school:last-of-type{margin-bottom:0}.school-details{font-size:1rem;display:flex;justify-content:space-between;align-items:flex-end}h3.school-name{font-size:1.2rem;margin:0;border-bottom:1px solid #21262e;font-weight:400;grid-row:1;display:flex;justify-content:space-between;align-items:flex-end}.degree{font-weight:300}.duration{font-weight:200;font-size:.8rem}.location{font-size:.9rem}.company::after{content:", "}.location{font-weight:100}.description{font-weight:200}
.job{margin-bottom:1rem}.job:last-of-type{margin-bottom:0}.job-details{font-size:1rem}h3.job-position{font-size:1.2rem;margin:0;border-bottom:1px solid #21262e;font-weight:400;grid-row:1;display:flex;justify-content:space-between;align-items:flex-end}.duration{font-weight:200;font-size:.8rem}.company,.location{font-size:.9rem}.company::after{content:", "}.location{font-weight:100}.description{font-weight:200;text-align:justify}
.skills-list{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:10px;gap:10px;grid-auto-rows:minmax(10px, auto)}.skill{display:flex;justify-content:space-between;align-items:flex-end;margin:0;font-size:1rem;font-weight:200;padding:0 1rem 0 0}h4.skill-name{display:inline;margin:0;font-weight:300}.level{font-weight:200}.skill-level{width:1rem;height:1rem;border-radius:50%;background:#f0f6fc}.skill-level.checked{background:#afbac5}
.resume{width:21cm;min-width:40vw;min-height:29.7cm;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background:#151b22;padding:2.5rem;border-radius:1rem;margin:4rem;font-size:2rem;color:#c9d1d9;font-family:"JetBrains Mono",monospace}.section-title{font-size:1.575rem;border-bottom:2px solid #333944;margin-bottom:.8rem;text-transform:uppercase;font-weight:500}.interests{font-size:.9rem;font-weight:300}.interest::after{content:", "}.interest:last-of-type::after{content:"."}a{text-decoration:none;color:#afbac5;transition:color .15s ease-in-out}a:hover{color:#206feb}section{margin-bottom:0}@media screen and (max-width: 768px){.resume{width:80vw;padding:2rem 1.5rem;margin:1rem 1vw;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.contact-info{flex-direction:column;align-items:flex-start}.skills-list{display:flex;flex-direction:column}}@media print{.resume{margin:0;padding:0;width:100%;height:100%;background:#fff;color:#151b22;border-radius:0}a{color:#151b22}.full-name{border-bottom:4px solid #151b22;line-height:2.5rem;margin-top:0}.section-title{border-bottom:2px solid #21262e}section{margin-bottom:0;padding-bottom:0}.school,.job{margin-bottom:0}.social-icons{display:none}}
